.home {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-background {
  height: 100%;
  width: 100%;
  background-image: url("../images/JDBHome.webp");
  background-position: center;
  background-size: cover;
  transition: all 20s;
  z-index: 80;
}

.home:hover .home-background,
.home:focus .home-background {
  transform: scale(1.2);
}

.home h1 {
  position: absolute;
  z-index: 100;
  font-family: 'Padaloma';
  font-weight: 300;
  letter-spacing: 4px;
}

.home h5 {
  margin-top: 200px;
  position: absolute;
  z-index: 100;
  font-family: 'Padaloma';
  font-weight: 300;
  letter-spacing: 4px;
}